import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import red from '@material-ui/core/colors/red';


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > * + *': {
      marginLeft: theme.spacing(2),
    },
  },
}));

function Input(props){

  const [input, setInput] = useState("");

  function handleChange(event){
    setInput(event.target.value);
  }

  function handleClic(event){
        props.urlCatch(input);
        event.preventDefault();
        setInput("");
        
    }

console.log(props.style);
  
    return(
        <div>
<section id="input">
  <form action="/">
    <input style={props.style} className="input user_url" id={props.className} name="user_url" type="text" onChange={handleChange} placeholder="Shorten a link here..." value={input}/>
    <button target="user_url" className="btn btn__big btn__square" type="submit" value="submit" onClick={handleClic}>Shorten It!</button>
    <div className="progress" style={props.progress} >
      <CircularProgress color="primary" className="circular" />
    </div>
  </form>
</section>
<p className="error" style={props.error}>Please enter a correct URL</p>
</div>
    )
}
  

export default Input;
